<script setup>
import { ref, onMounted, watch } from 'vue';
import { useRoute } from 'vue-router';
import axios from 'axios';
import { Line } from 'vue-chartjs';

// Import necessary Chart.js components
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement
} from 'chart.js';

ChartJS.register(Title, Tooltip, Legend, LineElement, CategoryScale, LinearScale, PointElement);

const route = useRoute();
const coinId = ref(route.params.id);
const coinData = ref({});
const chartData = ref(null);
const chartOptions = ref({
  responsive: true,
  maintainAspectRatio: false
});
const selectedDays = ref(30); // Default to 1 month
const secondCoin = ref('');
const secondChartData = ref(null);

const fetchCoinData = async () => {
  try {
    const response = await axios.get(`https://api.coingecko.com/api/v3/coins/${coinId.value}`);
    coinData.value = response.data;
  } catch (error) {
    console.error("‚ùå Error fetching coin data:", error);
  }
};

const fetchChartData = async () => {
  try {
    const response = await axios.get(`https://api.coingecko.com/api/v3/coins/${coinId.value}/market_chart`, {
      params: { vs_currency: 'usd', days: selectedDays.value }
    });

    chartData.value = {
      labels: response.data.prices.map(price => new Date(price[0]).toLocaleDateString()),
      datasets: [
        {
          label: `${coinData.value.name} Price`,
          data: response.data.prices.map(price => price[1]),
          borderColor: 'blue',
          backgroundColor: 'rgba(0, 0, 255, 0.1)',
          fill: true
        }
      ]
    };
  } catch (error) {
    console.error("‚ùå Error fetching chart data:", error);
  }
};

// Fetch data when component is mounted & when `selectedDays` changes
onMounted(() => {
  fetchCoinData();
  fetchChartData();
});

watch(selectedDays, fetchChartData);

const fetchSecondCoinData = async () => {
  if (!secondCoin.value) return;

  try {
    const response = await axios.get(`https://api.coingecko.com/api/v3/coins/${secondCoin.value}/market_chart`, {
      params: { vs_currency: 'usd', days: selectedDays.value }
    });

    secondChartData.value = {
      labels: response.data.prices.map(price => new Date(price[0]).toLocaleDateString()),
      datasets: [
        {
          label: `${secondCoin.value.toUpperCase()} Price`,
          data: response.data.prices.map(price => price[1]),
          borderColor: 'red',
          backgroundColor: 'rgba(255, 0, 0, 0.1)',
          fill: true
        }
      ]
    };
  } catch (error) {
    console.error("‚ùå Error fetching second coin data:", error);
  }
};
</script>

<template>
  <div>
    <h1>{{ coinData.name }} ({{ coinData.symbol?.toUpperCase() }})</h1>
    <p>Current Price: ${{ coinData.market_data?.current_price?.usd }}</p>

    <!-- Timeframe Selector -->
    <label for="timeframe">Select Timeframe:</label>
    <select v-model="selectedDays">
      <option value="1">24h</option>
      <option value="5">5 Days</option>
      <option value="7">1 Week</option>
      <option value="30">1 Month</option>
      <option value="180">6 Months</option>
      <option value="365">Year-to-Date</option>
      <option value="max">All Time</option>
    </select>

    <!-- Main Chart -->
    <div style="width: 100%; height: 400px;">
      <Line v-if="chartData" :data="chartData" :options="chartOptions" />
    </div>

    <!-- Second Coin Selector -->
    <h2>Compare with another coin</h2>
    <input type="text" v-model="secondCoin" placeholder="Enter coin ID (e.g., ethereum)" />
    <button @click="fetchSecondCoinData">Compare</button>

    <!-- Second Chart -->
    <div v-if="secondChartData" style="width: 100%; height: 400px;">
      <Line :data="secondChartData" :options="chartOptions" />
    </div>
  </div>
</template>

<style scoped>
h1 {
  text-align: center;
}

select {
  margin: 10px;
  padding: 5px;
}

input {
  margin: 10px;
  padding: 5px;
  width: 250px;
}

button {
  padding: 5px 10px;
  cursor: pointer;
}
</style>
