import { createRouter, createWebHashHistory } from 'vue-router'; // ‚úÖ Use hash mode for GitHub Pages
import HomeView from '../views/HomeView.vue';
import TradeView from '../views/TradeView.vue';
import WalletView from '../views/WalletView.vue';
import CoinDetailView from '../views/CoinDetailView.vue'; // ‚úÖ Import the new view

const routes = [
  { path: '/', component: HomeView },
  { path: '/trade', component: TradeView },
  { path: '/wallet', component: WalletView },
  { path: '/coin/:id', component: CoinDetailView } // ‚úÖ Dynamic route for individual coins
];

const router = createRouter({
  history: createWebHashHistory('/CoinDS/'), // ‚úÖ Hardcoded base URL for GitHub Pages
  routes,
});

export default router;
