<template>
  <div :class="{'dark-mode': isDarkMode}">
    <nav class="nav-bar">
      <h1 class="logo">üî• CoinDS Crypto Tracker üî•</h1>
      <ul>
        <li><router-link to="/">Home</router-link></li>
        <li><router-link to="/trade">Trade</router-link></li>
        <li><router-link to="/wallet">Wallet</router-link></li>
      </ul>
      <button @click="toggleDarkMode" class="toggle-button">
        {{ isDarkMode ? '‚òÄÔ∏è Light Mode' : 'üåô Dark Mode' }}
      </button>
    </nav>

    <main class="crypto-container">
      <h2 class="title">üöÄ Top 100 Crypto Prices üöÄ</h2>
      <input type="text" class="search-bar" placeholder="Search for a coin..." v-model="searchQuery">

      <table class="crypto-table">
        <thead>
          <tr>
            <th>Rank</th>
            <th>Coin</th>
            <th>Price</th>
            <th>24h Change (%)</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="coin in filteredCoins" :key="coin.id">
            <td>{{ coin.rank }}</td>
            <td>{{ coin.name }} ({{ coin.symbol.toUpperCase() }})</td>
            <td>${{ coin.price }}</td>
            <td :class="coin.change > 0 ? 'text-green' : 'text-red'">
              {{ coin.change }}%
            </td>
          </tr>
        </tbody>
      </table>
    </main>

    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isDarkMode: false,
      searchQuery: '',
      top100: [
        { id: 1, rank: 1, name: "Bitcoin", symbol: "BTC", price: 87614, change: 1.36 },
        { id: 2, rank: 2, name: "Ethereum", symbol: "ETH", price: 2178.87, change: 1.90 },
        { id: 3, rank: 3, name: "XRP", symbol: "XRP", price: 2.48, change: 4.22 },
        { id: 4, rank: 4, name: "Tether", symbol: "USDT", price: 1.00, change: 0.07 }
      ]
    };
  },
  computed: {
    filteredCoins() {
      return this.top100.filter(coin => 
        coin.name.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
        coin.symbol.toLowerCase().includes(this.searchQuery.toLowerCase())
      );
    }
  },
  methods: {
    toggleDarkMode() {
      this.isDarkMode = !this.isDarkMode;
      document.body.classList.toggle("dark-mode", this.isDarkMode);
    }
  }
};
</script>

<style>
/* ‚úÖ General Styling */
body {
  font-family: 'Poppins', sans-serif;
  background: linear-gradient(to right, #141e30, #243b55);
  color: white;
  transition: all 0.3s ease-in-out;
  margin: 0;
}

/* ‚úÖ Navigation Bar */
.nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #1a1a1a;
  padding: 15px;
  border-bottom: 3px solid #00d4ff;
  box-shadow: 0 4px 8px rgba(0, 212, 255, 0.2);
}

.nav-bar .logo {
  font-size: 22px;
  font-weight: bold;
  color: #00d4ff;
}

.nav-bar ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-bar ul li {
  display: inline;
}

.nav-bar ul li a {
  text-decoration: none;
  color: #00d4ff;
  font-weight: bold;
  font-size: 18px;
  transition: 0.3s;
}

.nav-bar ul li a:hover {
  color: #ff007f;
}

/* ‚úÖ Dark Mode Styling */
.dark-mode {
  background: linear-gradient(to right, #0f0f0f, #1a1a1a);
  color: white;
}

.dark-mode .nav-bar {
  background: #222;
}

.dark-mode .nav-bar ul li a {
  color: #00ffea;
}

.dark-mode .nav-bar ul li a:hover {
  color: #ff007f;
}

/* ‚úÖ Crypto Table */
.crypto-container {
  text-align: center;
  padding: 30px;
}

.title {
  font-size: 28px;
  font-weight: bold;
  color: #00d4ff;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.8);
}

.search-bar {
  width: 50%;
  padding: 10px;
  font-size: 16px;
  border-radius: 5px;
  border: none;
  margin-bottom: 20px;
  background: #1a1a1a;
  color: white;
  text-align: center;
}

.crypto-table {
  width: 80%;
  margin: 0 auto;
  border-collapse: collapse;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  overflow: hidden;
}

.crypto-table th, .crypto-table td {
  padding: 15px;
  text-align: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.crypto-table th {
  background: rgba(0, 212, 255, 0.2);
  color: white;
}

.crypto-table tr:hover {
  background: rgba(255, 255, 255, 0.1);
}

/* ‚úÖ Green/Red for Price Changes */
.text-green {
  color: #32CD32;
  font-weight: bold;
  text-shadow: 0 0 5px #32CD32;
}

.text-red {
  color: #FF5733;
  font-weight: bold;
  text-shadow: 0 0 5px #FF5733;
}

/* ‚úÖ Dark Mode Toggle Button */
.toggle-button {
  background: #00d4ff;
  color: black;
  padding: 8px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.3s;
}

.toggle-button:hover {
  background: #ff007f;
  color: white;
}
</style>
